export const housesCavaloWalk = (element: any) => {
  return [
    { x: (element.x || 0) - 2, y: (element.y || 0) - 1 },
    { x: (element.x || 0) - 1, y: (element.y || 0) - 2 },
    { x: (element.x || 0) - 2, y: (element.y || 0) + 1 },
    { x: (element.x || 0) - 1, y: (element.y || 0) + 2 },
    { x: (element.x || 0) + 2, y: (element.y || 0) - 1 },
    { x: (element.x || 0) + 1, y: (element.y || 0) - 2 },
    { x: (element.x || 0) + 2, y: (element.y || 0) + 1 },
    { x: (element.x || 0) + 1, y: (element.y || 0) + 2 },
  ]
}

export const housesReiWalk = (element: any) => {
  return [
    { x: (element.x || 0) - 1, y: (element.y || 0) - 1 },
    { x: (element.x || 0) - 1, y: element.y || 0 },
    { x: (element.x || 0) - 1, y: (element.y || 0) + 1 },
    { x: element.x || 0, y: (element.y || 0) - 1 },
    { x: element.x || 0, y: (element.y || 0) + 1 },
    { x: (element.x || 0) + 1, y: (element.y || 0) - 1 },
    { x: (element.x || 0) + 1, y: element.y || 0 },
    { x: (element.x || 0) + 1, y: (element.y || 0) + 1 },
  ]
}

export const housesTorreWalk = (element: any, sizes: any) => {
  const _housesKing = housesReiWalk(element)
  const housesKing = [_housesKing[1], _housesKing[3], _housesKing[4], _housesKing[6]]
  const housesTorre: any[] = []

  housesKing.forEach((element, idx) => {
    let x = element.x
    let y = element.y

    if (idx === 0) {
      while (x > 0) {
        x = x - 1
        housesTorre.push({ x, y })
      }
    } else if (idx === 1) {
      while (y > 0) {
        y = y - 1
        housesTorre.push({ x, y })
      }
    } else if (idx === 2) {
      while (y < sizes.y) {
        y = y + 1
        housesTorre.push({ x, y })
      }
    } else if (idx === 3) {
      while (x < sizes.x) {
        x = x + 1
        housesTorre.push({ x, y })
      }
    }
  })

  return [...housesKing, ...housesTorre]
}

export const housesPeaoWalk = (element: any) => {
  const _housesKing = housesReiWalk(element)
  const housesPeao = [_housesKing[1], _housesKing[3], _housesKing[4], _housesKing[6]]
  return housesPeao
}

export const housesRainhaWalk = (element: any, sizes: any) => {
  const bispo = housesBispoWalk(element, sizes)
  const torre = housesTorreWalk(element, sizes)

  return [...bispo, ...torre]
}

export const housesBispoWalk = (element: any, sizes: any) => {
  const _housesKing = housesReiWalk(element)
  const housesKing = [_housesKing[0], _housesKing[2], _housesKing[5], _housesKing[7]]
  const housesBispo: any[] = []

  housesKing.forEach((element, idx) => {
    let x = element.x
    let y = element.y

    if (idx === 0) {
      while (x > 0 && y > 0) {
        x = x - 1
        y = y - 1
        housesBispo.push({ x, y })
      }
    } else if (idx === 1) {
      while (x > 0 && y < sizes.y) {
        x = x - 1
        y = y + 1
        housesBispo.push({ x, y })
      }
    } else if (idx === 2) {
      while (y > 0 && x < sizes.x) {
        y = y - 1
        x = x + 1
        housesBispo.push({ x, y })
      }
    } else if (idx === 3) {
      while (y < sizes.y && x < sizes.x) {
        x = x + 1
        y = y + 1
        housesBispo.push({ x, y })
      }
    }
  })

  return [...housesKing, ...housesBispo]
}